import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WelfareItemMasterComponent } from './welfare-item-master.component';
import { CommonModule } from '@angular/common';

const routes: Routes = [
  {
      path: '', 
      component: WelfareItemMasterComponent,
     
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WelfareItemMasterRoutingModule{}


