import { Component,ChangeDetectorRef, OnInit,Input,Output,Inject,EventEmitter,ViewChild } from '@angular/core';
import {FormGroup,FormControl,FormBuilder,Validators } from '@angular/forms';
import {WelfareItemMasterService, WelfareItem, CurrentUserService,ErrorHandleService} from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-welfare-item-master-edit',
  templateUrl: './welfare-item-master-edit.component.html',
  styleUrls: ['./welfare-item-master-edit.component.scss']
})
export class WelfareItemMasterEditComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @Input('WelfareItemEditData') WelfareItemEditData;
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('lComboBox') lComboBox: jqxComboBoxComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

    EditData:any;
    welfareForm:FormGroup;
    welfareData:WelfareItem;
    apiUrl:string;
    item_data:any;
   itemAdapter:any;
   lAdapter:any[]=[
    { value: '1', label: 'Yes' },
    { value: '0', label: 'No '}
   ];  

   userData: any = {};

    constructor( private fb:FormBuilder,
      private wITS: WelfareItemMasterService,
      private ehs: ErrorHandleService,
      @Inject('API_URL') apiUrl: string,
      private cus: CurrentUserService,
      private cdr:ChangeDetectorRef) { 
        this.apiUrl = apiUrl;
        this.userData = this.cus.getTokenData();
        this.createForm();
     }
  
    ngOnInit() {
     this.welfareData=new WelfareItem(this.WelfareItemEditData);
      this.welfareForm.setValue(this.welfareData);
      this.item_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'item?limit=all',
      };
    this.itemAdapter = new jqx.dataAdapter(this.item_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      },loadComplete: () => {
        this.myComboBox.selectItem(this.WelfareItemEditData.item_id);
        }
    });
    }
    ngAfterViewInit(): void 
    {
      this.lComboBox.selectItem(this.WelfareItemEditData.link_with_store_item);
     this.cdr.detectChanges();
    }
    createForm() {
      this.welfareForm = this.fb.group({ 
        'code': [null,Validators.required],
        'name': [null,Validators.required],
       // 'link_with_store_item': [null,Validators.required],  
      });
    }
    saveBtn(post){ 
      post['item_id'] = this.myComboBox.val();
      post['link_with_store_item'] = this.lComboBox.val();
     this.myValidator.validate(document.getElementById('welfareForm'));
     if (post['item_id'] && post['link_with_store_item']) {
      this.jqxLoader.open();
      this.wITS.update(this.WelfareItemEditData.id,post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
      });
    }
    }

    cancelBtn(){
      this.cancelData.emit(true);
    }

    rules =
    [
       
      { input: '.code', message: 'Code is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.name', message: 'Name is required!', action: 'keydown, blur', rule: 'required' },
      { input: '.link', message: 'link is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
       let data = this.lComboBox.val();
       let result = (data!='')?true:false;
       return result;
     } },
      { input: '.item_id', message: 'Item name is required!', action: 'keydown, blur,change',rule: (input: any, commit: any): any => {
       let data = this.myComboBox.val();
       let result = (data!='')?true:false;
       return result;
     }
    },  
    ];     
}



