import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { WelfareItemMasterComponent } from './welfare-item-master.component';
import { WelfareItemMasterRoutingModule } from './welfare-item-master-routing.module';
import { WelfareItemMasterAddComponent } from './welfare-item-master-add/welfare-item-master-add.component';
import { WelfareItemMasterEditComponent } from './welfare-item-master-edit/welfare-item-master-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    WelfareItemMasterRoutingModule,
  ],
  entryComponents:[
    WelfareItemMasterAddComponent,
    WelfareItemMasterEditComponent
  ],
  declarations: [
    WelfareItemMasterComponent,
    WelfareItemMasterAddComponent,
     WelfareItemMasterEditComponent
    ]
})
export class WelfareItemMasterModule { }



